<template>
  <div>
    <div class="text-center pa-1 footer d-none d-md-block">
      Copyright &#169; {{ new Date(Date.now()).getFullYear() }} | Meu Barbeiro
    </div>
    <v-bottom-navigation
      v-model="bottomNavModel"
      padless
      :inset="false"
      fixed
      grow
      shift
      class="primary d-md-none"
      dark
    >
          <v-btn
          v-for="(item, indice) in items"
        :key="indice"
        class="bottom-nav-item"
          :value="item.value"
          @click="goTo(item)"
        >
          <div
            v-if="item.title === 'Solicitaes' && qtdRequestsPendingState > 0"
            class="bottom-nav-badge success primary--text"
          >{{ qtdRequestsPendingState }}</div>
            <span>{{ item.title }}</span>
            <v-icon>{{ item.icon }}</v-icon>
          
        </v-btn>
    </v-bottom-navigation>
  </div>
</template>

<script>
import ScheduleMixin from '@/mixins/ScheduleMixin'

export default {
  name: 'Footer',
  props: {
    items: { default: Array },
    bottomNav: { default: '' }
  },
  mixins: [ ScheduleMixin ],
  computed: {
    bottomNavModel: {
      set () {},
      get () {
        return this.$route.meta.bottomBar
      }
    }
  },
  methods: {
    goTo (item) {
      this.$emit('clicked', item)
    }
  }
}
</script>

